.body {
  background-color: var(--white);
  color: var(--black);
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: var(--bold);
  letter-spacing: -.56px;
  line-height: 1.2;
}

[data-theme="dark"] {
  background-color: var(--black-cat);
  color: var(--white);
}

.header {
  background: var(--white);
  border-style: solid;
  border-color: var(--black);
  width: 100%;
  border-width: 1.5px;
  border-radius: 0 0 8px 8px;
  position: absolute;
}

@media (min-width: 1440px) {
  .header {
    width: 1440px;
    top: 0;
    left: 50%;
    transform: translate(-50%);
  }
}

.header__content {
  justify-content: space-between;
  align-items: center;
  padding: 18px 20px;
  display: flex;
}

@media (min-width: 768px) {
  .header__content {
    padding: 14px 32px;
  }
}

@media (min-width: 1440px) {
  .header__content {
    padding: 14px 24px;
  }
}

.header__logo {
  align-items: center;
  gap: 8px;
  margin-right: 76px;
  display: flex;
}

@media (max-width: 768px) {
  .header__logo {
    z-index: 1;
  }
}

.logo__title {
  color: var(--black);
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: var(--bold);
  letter-spacing: -.56px;
  line-height: 18px;
}

.menu__list {
  z-index: 1;
  align-items: center;
  gap: 24px;
  display: flex;
}

@media (max-width: 768px) {
  .menu__list {
    display: none;
  }

  .menu__list.is-open {
    z-index: 2;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    display: flex;
    position: fixed;
    top: 248px;
    left: 50%;
    transform: translate(-50%);
  }
}

.menu__link {
  text-transform: uppercase;
  color: var(--black);
  fill: var(--black);
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: var(--medium);
  letter-spacing: -.56px;
  align-items: center;
  gap: 4px;
  line-height: 18px;
  display: flex;
}

.menu__link:hover, .menu__link:focus {
  color: var(--purple);
  fill: var(--purple);
}

@media (max-width: 768px) {
  .menu__link {
    color: var(--white);
    fill: var(--white);
  }

  .menu__link:hover, .menu__link:focus {
    color: var(--black);
    fill: var(--black);
  }
}

.menu__link--current {
  font-weight: var(--bold);
  background: var(--yellow);
  color: var(--black);
  border-radius: 36px;
  padding: 8px 16px;
}

@media (max-width: 768px) {
