:root {
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --italic: italic;
  --black: #111;
  --black-cat: rgba(17, 17, 17, .6);
  --white-cat: rgba(255, 255, 255, .6);
  --grey-400: #b4afaf;
  --white: #fff;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --filter-light: 0%;
  --filter-dark: invert(100%);
}

p, h1, h2, h3, h4, h5, h6, ul, li {
  margin: 0;
  padding: 0;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

input, textarea {
  cursor: auto;
}

img, picture, svg {
  max-width: 100%;
  display: block;
}

button {
  font: inherit;
  cursor: pointer;
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 428px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
