}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

#darkSwitch {
  display: none;
}

label {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 200px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #f7f7f7;
  border-radius: 180px;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

label p {
  display: none;
}

input {
  width: 0;
  height: 0;
  visibility: hidden;
}

input:checked + label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + label:after {
  background: none, #f7f7f7;
  right: 24px;
  transform: translateX(110%);
}

label, label:after {
  transition: all .3s;
}

.visual-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

