.menu__btn {
    display: flex;
    background-color: transparent;
    padding: 0;
    margin-left: 14px;
  
    @media (width > 768px) {
      display: none;
    }
  }
  
  @media (width <= 768px) {
    .menu__icon {
      display: block;
      stroke: var(--black);
      transition: all $cubic;
    }
  
    .menu__icon:hover {
      stroke: var(--blue);
    }
  
    .menu__icon-menu,
    .is-open .menu__icon-cross {
      display: block;
    }
  
    .menu__icon-cross,
    .is-open .menu__icon-menu {
      display: none;
    }
  }
  
  .menu__substrate {
    display: none;
    position: fixed;
    top: 66px;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: url(/src/images/menu/menu_background.png),
      linear-gradient(var(--purple), var(--black));
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
  
    @media (width <= 768px) {
      &.is-open {
        display: block;
      }
    }
  }
  
  .no-scroll {
    overflow: hidden;
  }